<template>

<v-app>

  <v-container>
    <v-subheader light>
      <h2>Products</h2>
      <v-spacer></v-spacer>
      <small>Home | <small>Ets Pamasa</small></small>
    </v-subheader>
    <br>
    <v-layout row wrap ml-0>

    <v-flex sm5 xs12 md5 lg4>
        <v-card>
            <v-layout wrap>
            <v-container>
                <v-container>
                <span>Factures</span>
                <v-spacer></v-spacer>
                  
                    <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                    ></v-text-field>
                </v-container>
                <v-data-table
                :headers="headers"
                :items="ventes"
                :items-per-page="10"
                :search="search"
                >
                    <template v-slot:body="{items}">
                        <tbody>
                            <tr v-for="(client,index) in items" :key="client.id">
