                                <td>{{client.code}}</td>   
                                <td>{{client.created_at}}</td>
                                <td>
                                    <small><i><v-btn x-small color="success" @click="afficherinvoice(client.id,client.created_at)">Afficher</v-btn></i></small>
                                </td>   
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
                
            </v-container>
        </v-layout>
        </v-card>
    </v-flex>
    <v-flex sm7 xs12 md7 lg8 pl-1>
        <v-card class="invoice-box">
            <v-card-text>
            <div>
              <table cellpadding="0" cellspacing="0" top="-10">
                <tr>
                  <td colspan="4">
                    <table style="align:center;">
                      <tr>
                        <td>Ets PAMASA
                          <br>PHARMACY AGROVET <span class="text-uppercase">{{shop.names}}</span>
                            <br>CD/GOM/16-A-1128 <br>Av. {{shop.address}}/Butembo, Nord-kivu <br>Tel: +243991471379, +243990358342
                            <br> <a href="maito:info@etspamasa.com"><span> info@etspamasa.com</span></a>
                        </td>

                        <td>
                              Noms: {{client.names}}
                            <br>
                              Address: {{client.address}}
                              <br> Tele: {{client.phone}}
                              <br>Email: {{client.email}}
                        </td>
                      </tr>
                      <hr>
                    </table>
                  </td>
                </tr>

                <tr class="information" style="margin-top:-300px;">
                  <td colspan="4">
                    <table>
                      <tr>
                        <td style="padding-top:-50px;">
                          Facture#: {{invoiceId}}
                        </td>
                        <td>
                            Date: {{date}}
                        </td>
                      </tr>
                    </table>
                  </td>
                </tr>

                <tr class="heading">
                  <td colspan="3">Mode de payement</td>
                  <td>Cash #</td>
                </tr>

                <tr class="details">
                  <td colspan="3">Cash</td>
                  <td>{{ total | currency }}</td>
                </tr>

                <tr class="heading">
                  <td>#. Item</td>
                  <td>Unit</td>
                  <td>Quantity</td>
                  <td>Price</td>
                </tr>

                <tr class="item" id="tbody" v-for="(item,index) in itemsc" :key="index">
                    <td><span>{{index+1}} </span> <span>{{item.produit}}</span></td>
                    <td>$ {{item.amount}}</td>
                    <td>$ {{item.quantity}}</td>
                    <td>$ {{item.amount * item.quantity | currency}}</td>
                  <!-- <td><span>{{index+1}} </span><input type="text" :disabled="disabledinvoice"   v-model="item.produit" /></td>
                  <td>$<input type="number" :disabled="disabledinvoice" v-model="item.amount" /></td>
                  <td><input type="number" :disabled="disabledinvoice" v-model="item.quantity" /></td>
                  <td>${{ item.amount * item.quantity | currency }}</td> -->

                </tr>

                <tr>
                  <td colspan="4">
                    <button class="btn-add-row"></button>
                  </td>
                </tr>

                <tr class="total">
                  <td colspan="3"></td>
                  <td>Total: ${{ total | currency }}</td>
                </tr>
                <tr class="heading">
                  <td>
                       <small><i>La marchandise vendue n'est peut etre ni remise ni echange</i></small>
                  </td>
                </tr>
                <br>
                <tr v-if="clientId !==null">
                  <v-btn x-small  color="default" @click="print">Print</v-btn>
                </tr>
              </table>
            </div>
          </v-card-text> 
        </v-card>
    </v-flex>

    </v-layout>
  </v-container>
</v-app> 
</template>

<script>
  export default {
    name:"Ventes",
    data () {
      return {
            ventes:[],
            clients: [],
            client:{names:'',phone:'',address:'',email:''},
            invoiceId: '',
            itemsc: [],
            search:'',
            date:'',
            clientId:null,
            headers: [
            {
                text: '#',
                align: 'start',
                value: 'id',
            },
            {
                text: 'Code',
                align: 'start',
                value: 'code',
            },
            { text: 'Date', value: 'created_at' },
            { text: 'Actions', value: 'iron' },
            ],
        }
    },  
    computed: {
        total() {
        return this.itemsc.reduce(
            (acc, item) => acc + item.amount * item.quantity,
            0
        )
        },
        shop(){
          let names = JSON.parse(localStorage.getItem('branche'))
          return names
        }
    },
    filters: {
        currency(value) {
        return value.toFixed(2);
        }
    },
    methods:{
        getClient(){
            let id = this.$route.params.id
            this.$api.get('clientallventes/'+id)
            .then(res=>{
                this.ventes=res.data
                localStorage.setItem('date',res.data.created_at)
            })
        },
        afficherinvoice(id,date){
          this.date=date
            this.$api.get('detailsventes/'+id)
            .then(rs=>{
                this.itemsc = rs.data.records
                this.client.names = rs.data.records[0].names
                this.client.phone = rs.data.records[0].telephone
                this.client.address = rs.data.records[0].address
                this.client.email = rs.data.records[0].email
                this.invoiceId = rs.data.records[0].invoiceid
                this.clientId = id
                // console.log(rs.data.records[0].address)
            })
        },
        print(){
            localStorage.setItem('client',JSON.stringify(this.client))
            localStorage.setItem('invoice',this.invoiceId)
            localStorage.setItem('date',this.date)
            localStorage.setItem('records',JSON.stringify(this.itemsc))
            this.$router.push('/print')
            this.$store.dispatch('isPrinting')
        }
    },
    mounted(){ 
        this.getClient()
    },
  }
</script>
