<template>
  <v-app>
        <v-subheader light>
            <h2>Produits <span style='color:red'></span></h2>
            <v-spacer></v-spacer>
            <small>Articles | <small>Ets Pamasa</small></small>
        </v-subheader>
        <br>

        <v-alert text tile :value="alert" dismissible border="left" type="success" dense>{{msg}}</v-alert>
        <v-alert text tile :value="error" dismissible border="left" type="error" dense>{{msg}}</v-alert>
    <v-card width="100%" tile  elevation="2" >
      <v-card-title>Produits
        <v-spacer></v-spacer>
        <v-btn @click="tonew" small color="#1D8A69" class="white--text">
          <span>Approvisionner</span>
        </v-btn>
      </v-card-title>
      <v-divider></v-divider>
      <v-container>
        <v-layout wrap row top="5">
          <v-container>
            <v-row>
              <v-col class="12" md="4">
                <v-card>
                  <v-card-title style="background-color: #1D8A69;">
                    <span class="font-weight-light white--text text-uppercase font-weight-light">{{(product.id ? "Modification du Produit" : "Enregistrement du Produit")}}</span>
                  </v-card-title>
                  <v-card-text>
                    <validation-observer ref="observer" v-slot="{ invalid }">
                      <form @submit.prevent="submit">
                          <v-container
                            >
                            <v-row>
                            <v-col
                              cols="12"
                              md="12"
                            >
                            <validation-provider
                              v-slot="{ errors }"
                              class="d-flex"
                              cols="12"
                              sm="12"
                              name="Nom du Produit"
                              rules="required|max:50"
                            >
                              <v-text-field
                                v-model="product.names"
                                :counter="50"
                                :error-messages="errors"
                                label="Nom du Produit"
                                
                                dense
                                required
                              ></v-text-field>
                            </validation-provider>
                            </v-col>
                            <v-col
                              cols="12"
                              md="12"
                            >
                            <validation-provider
                              v-slot="{ errors }"
                              class="d-flex"
                              cols="12"
                              sm="12"
                              name="Categorie du produit"
                              rules="required|max:50"
                            >
                              <v-select
                                v-model="product.category"
                                :counter="50"
                                :items="category_items"
                                :error-messages="errors"
                                label="Categorie du produit"
                                auto-select-first
                                dense
                                required
                              ></v-select>
                            </validation-provider>
                            </v-col>

                            <v-col
                              cols="12"
                              md="12"
                            >
                            <validation-provider
                              v-slot="{ errors }"
                              class="d-flex"
                              cols="12"
                              sm="12"
                              name="Statut du produit"
                              rules="required|max:50"
                            >
                              <v-select
                                v-model="product.status"
                                :counter="50"
                                auto-select-first
                                :items="status"
                                :error-messages="errors"
                                label="Statut du produit"
                                
                                dense
                                required
                              ></v-select>
                            </validation-provider>
                            </v-col>
                            </v-row>
                          </v-container>

                          <v-divider></v-divider>

                          <v-card-actions>
                            <v-btn @click="clear" color="error">
                              Annuler
                            </v-btn>
                            <v-spacer></v-spacer>


                            <v-btn class="mr-4" type="submit" :disabled="invalid"
                              color="green"
                              @click="manipulate_product"
                              >
                              {{(product.id ? "Modifier" : "Enregistrer")}}
                            </v-btn>
                          </v-card-actions>
                          </form>
                          </validation-observer>
                        </v-card-text>
                </v-card>
              </v-col>
              <v-col cols="12" md="8">
                  <v-card>
                    <v-card-title>
                      <span>Liste des Produits</span>
                      <v-spacer></v-spacer>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Recherche..."
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      dense
                      :headers="headers"
                      :items="post"
                      :search="search"
                    >
                    <template v-slot:body="{items}">
                      <tbody>
                        <tr v-for="(product,index) in items" :key="index">
                          <td>{{index+1}}</td>
                          <td>{{product.names}}</td>
                          <td>{{product.category}}</td>
                          <td>{{product.status}}</td>
                          <td>
                            <small>
                              <i>
                                <v-btn x-small color="success" @click="editTutorial(product)">
                                  <v-icon x-small>mdi-pencil</v-icon>
                                Modifier
                                </v-btn>
                              </i>
                            </small>
                          </td>
                            
                        </tr>
                      </tbody>
                    </template>
                    </v-data-table>
                  </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-layout>
      </v-container>
      <br>
    </v-card>
  </v-app>
</template>
<script>
import { required, digits,max, regex } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("eager");

extend("digits", {
  ...digits,
  message: "{_field_} needs to be {length} digits. ({_value_})",
});

extend("required", {
  ...required,
  message: "{_field_} ne peut pas etre vide",
});

extend("max", {
  ...max,
  message: "{_field_} ne peut pas depasser {length} characters",
});

extend("regex", {
  ...regex,
  message: "{_field_} {_value_} ne correspond pas {regex}",
});


export default {
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  data: () => ({
    post : [],
    alert: null,
    msg:'',
    error:false,
    product: {names:"",id:"",category:"",status:''},
    emballage_item: ["Carton","Bouteille","Sachet"],
    category_items: ["Veterinaire", "Agriculture", "Materiel","Nourriture"],
    status:['Active','Desactive'],
    search: "",
    headers: [
       {
        text: "#",
        filterable: false,
        align: "start",
        value: "id",
      },
      {
        text: "Produit (Nom)",
        value: "names",
      },
      { text: "Categorie", value: "category" },

      { text: "Statut", value: "status" },
      { text: "Actions" },
    ],
  }),

  methods: {
    submit () {
        this.$refs.observer.validate()
      },
    clear() {
      this.product= {names:"",id:"",category:"",status:''},
      this.$refs.observer.reset()
    },
    manipulate_product(){
      const form  = new FormData()
      form.append('token',this.$store.state.user.token)
      form.append('names',this.product.names)
      form.append('category',this.product.category)
      form.append('id',this.product.id)
      form.append('status',this.product.status)
      this.$api.post("/manipulate_product",form)
      .then(rs=>{
          if(rs.data.success){
            this.msg=rs.data.success
            this.alert = true
            setTimeout(()=>{
                this.alert=false
            },5000)
            this.shproducts()
            this.clear()
          }
          else{
            this.msg=rs.data.error
            this.error = true
            setTimeout(()=>{
              this.error=false
            },5000)
          } 
        })
      .catch(rs=>{
        this.msg=rs.data.error
        this.error = true
        setTimeout(()=>{
          this.error=false
        },5000)
      })
    },
    editTutorial(prod){
      this.product = prod
    },
    shproducts(){
      this.$api
      .get('/products')
      .then(res=>{
        this.post = res.data
      })
    },

    // fromto(obj){
    //   const fm = new FormData()
    //   for(var i in obj){
    //     fm.append(i,obj[i])
    //   }
    //   return fm
    // },
    tonew(){
      this.$router.push('new')
    }
  },
  mounted(){
    this.shproducts()
    document.title="Produit | ETSPAMASA"
  },
}
</script>
