<template>
<v-app>
    <span>
        <v-alert text tile :value="alert" dismissible border="left" type="success" dense>{{msg}}</v-alert>
        <v-alert text tile :value="error" dismissible border="left" type="error" dense>{{msg}}</v-alert>
    </span>
    <v-layout row wrap>
    <v-flex sm6 xs12 md6 lg4>
        <v-card>  
        <form @submit.prevent="manipulate_client">
           <v-card-title style="background: #1D8A69" class="white--text">
             <span>{{(client.id ? "Mis en jours du client" : "Enregistrement du Nouveau Client")}}</span>
           </v-card-title>
           <v-divider></v-divider>
           <v-card-text> 
             <br>
                <v-row>
                <v-col
                    cols="12"
                    md="12"
                >
                    <v-text-field
                    v-model="client.names"
                    :rules="nameRules"
                    :counter="50"
                    type="text"
                    dense
                    label="Noms Complet du client"
                    required
                    ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    md="6"
                >
                    <v-text-field
                    v-model="client.email"
                    :counter="50"
                    type="email"
                    :rule="emailRules"
                    dense
                    label="Email address"
                    ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    md="6"
                >
                    <v-text-field
                    v-model="client.telephone"
                    :rules="phoneRules"
                    :counter="10"
                    dense
                    type="number"
                    label="Telephone"
                    required
                    ></v-text-field>
                </v-col>

                <v-col
                    cols="12"
                    md="12"
                >
                    <v-text-field
                    v-model="client.address"
                    :counter="10"
                    type="text"
                    dense
                    label="Addresse du client"
                    ></v-text-field>
                </v-col>
                </v-row>
            </v-card-text>
            <br>
           <v-divider></v-divider>
           <v-card-actions>
           <v-btn
             color="grye"
             rounded
             @click="clear"
           >

           <span >Annuler</span>
           </v-btn>
           <v-spacer></v-spacer>
           <v-btn
             color="#1D8A69"
             rounded
             :loading="loading"
             @click="manipulate_client()"
           >

           <span >{{(client.id ? "Modifier" : "Enregistrer")}}</span>
           </v-btn>
           </v-card-actions>
           <br>
           </form>
        </v-card>
        </v-flex>
        <v-flex sm6 xs12 md6 lg8 pl-2>
                <v-card >
                    <v-card-title>
                        <span>Liste de clients</span>
                        <v-spacer></v-spacer>
                    <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                    ></v-text-field>
                    </v-card-title>
                    <v-data-table
                    :headers="headers"
                    :items="clients"
                    :items-per-page="5"
                    :search="search"
                    loading-text="En cours... Veuillez Patienter"
                    :loading="dataloading"
                    >
                    <template v-slot:body="{items}">
                        <tbody v-if="items.length">
                            <tr v-for="(client,index) in items" :key="client.id">
                                <td>{{index+1}}</td>
                                <td>{{client.names}}</td>  
                                <td>{{client.email}}</td>  
                                <td>{{client.telephone}}</td>
                                <td>
                                    <small><i><v-btn x-small color="success" @click="viewinvoices(client.id)">Fact</v-btn></i></small>
                                    <small><i><v-btn x-small color="info" @click="modifier_client(client)">Modifier</v-btn></i></small>
                                    <small><i><v-btn x-small color="error" @click="supprimer_client(client.id)">Supprimer</v-btn></i></small>
                                </td>   
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr>
                                <td colspan="5">Pas de donnees</td>
                            </tr>
                        </tbody>
                    </template>
                    </v-data-table>
                </v-card>
        </v-flex>
    </v-layout>
</v-app>
</template>
<!-- <script src="https://cdnjs.cloudflare.com/ajax/libs/axios/0.18.1/axios.min.js"></script> -->
<script>
export default {
    data(){
        return{
            msgAlert: null,
            dataloading:false,
            client: {names:"",address:"",telephone:"",email:"",id:""},
            names: '',
            update_id:null,
            loading:false,
            address: '',
            phone: '',
            nameRules: [
                v => !!v || 'Noms du client ne peux pas etre vide',
                v => v.length <= 50 || 'Noms ne doit pas depasser 50 caracteres',
            ],
            email: '',
            phoneRules: [
                v => !!v || 'Le numero de telephone est obligatoire',
                // v => /.+@.+/.test(v) || 'E-mail must be valid',
            ],
            emailRules: [
                // v => !!v || 'Address email est obligatoire',
                v => /.+@.+/.test(v) || 'E-mail doit etre valide',
            ],
            clients: [],
            search: '',
            headers: [
            {
                text: '#',
                align: 'start',
                filterable: false,
                value: 'id',
            },
            {
                text: 'Noms',
                align: 'start',
                // filterable: false,
                value: 'names',
            },
            { text: 'Email', value: 'email' },
            { text: 'Telephone', value: 'telephone' },
            { text: 'Actions', value: 'iron' },
            ],
            alert:false,
            msg:'',
            error: false,
            }
        },
        methods:{
            resetValidation () {
                this.$refs.form.resetValidation()
            },
            clear(){
                this.client = {names:"",address:"",telephone:"",email:"",id:""}
            },
            manipulate_client(){
                if(this.client.names !==''){
                // const form = this.formto(this.client)
                    this.loading=true
                    const form = new FormData()
                    form.append('names',this.client.names)
                    form.append('address',this.client.address)
                    form.append('telephone',this.client.telephone)
                    form.append('email',this.client.email)
                    form.append('id',this.client.id)
                    form.append('branche',this.$store.state.user.brancheid)
                    form.append('token',this.$store.state.user.token)
                    form.append('clientId','')
                    this.$api.post('manipulate_client',form)
                    .then(rs=>{
                        if(rs.data.success){
                            this.getClients()
                            this.clear()
                            this.msg=rs.data.success
                            this.alert = true
                            this.loading = false
                            setTimeout(()=>{
                                this.alert=false
                            },5000)
                        }
                        else{
                            this.msg=rs.data.error
                            this.error = true
                            this.loading = false
                            setTimeout(()=>{
                                this.error=false
                            },5000)
                        }
                    })
                }else{
                    this.msg="Veullez Ajouter le nom du client"
                    this.error = true
                    this.loading = false
                    setTimeout(()=>{
                        this.error=false
                    },5000)
                }
            },
            getClients(){
                this.dataloading = true
                let id = this.$store.state.user.brancheid
                this.$api.post('clients/'+id)
                .then(res=>{
                    this.clients=res.data.clients
                    this.dataloading = false
                })
            },
            viewinvoices(id){
                this.$router.push('/archive/'+id)
            },
            modifier_client(client){
                this.client =client
            },
            supprimer_client(id){
                if(window.confirm("Voulez-vous souhaiter supprimer ce client")){
                    const up_id = new FormData()
                    up_id.append('id',id)
                    this.$api.post('delete_client',up_id)
                    .then(rs=>{
                        if(rs.data.success){
                        this.getClients()
                        this.clear()
                        this.msg=rs.data.success
                        this.alert = true
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                    }
                    else{
                        this.msg=rs.data.error
                        this.error = true
                        setTimeout(()=>{
                            this.error=false
                        },5000)
                    }
                    })
                }
            },
            // formto(obj){
            //     const fo = new FormData()
            //     for(var i in obj){
            //         fo.append(i,obj[i])
            //     }
            //     return fo
            // }
        },
        mounted(){
            this.getClients()
            document.title="Clients | ETSPAMASA"
        }
    
    }
</script>