                  >
                  <validation-provider
                    v-slot="{ errors }"
                    name="Qte a ventre"
                    class="d-flex"
                    cols="12"
                    sm="6"
                  >
                    <v-text-field
                      v-model="formitems.qty"
                      type="number"
                      :error-messages="errors"
                      label="Qte a ventre"
                      dense
                      required
                    ></v-text-field>
                  </validation-provider>
                  </v-col>
                  <v-col
                    cols="12"
                    md="6"
                  >
                  <validation-provider
                    v-slot="{ errors }"
                    name="PVU"
                    class="d-flex"
                    cols="12"
                    sm="6"
                  >
                    <v-text-field
                      v-model="formitems.pv"
                      type="number"
                      :error-messages="errors"
                      label="Prix Unitaire de vente"
                      dense
                      required
                    ></v-text-field>
                  </validation-provider>
                  </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <br>
              <v-divider></v-divider>
              <v-card-actions>
              <v-btn
                color="grye"
                rounded
                @click="clear"
              >

              <span >Annuler</span>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn
                color="#1D8A69"
                rounded
                :loading="loading"
                :disabled="invalid"
                @click="submitvente();"
              >

              <span >Confirmer</span>
              </v-btn>
              </v-card-actions>
              <br>
            </form>
          </validation-observer>
        </v-card>

    </v-flex>
    <v-dialog v-model="dialogItem" width="900">
        <v-card>
          <v-card-title>
            Veuillez Selectionner le produit
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
                    <v-flex ml-1>
                          <v-simple-table dense fixed-header>
                                        <template v-slot:default>
                                          <thead>
                                            <tr>
                                              <th>N#</th>
                                              <th class="text-left">
                                                Product
                                              </th>
                                              <th>Qte Disp</th>
                                              <!-- <th>Batch</th> -->
                                              <!-- <th>PA</th> -->
                                              <th>PV</th>
                                              <th>Discount</th>
                                              <th>Date d'Appr</th>
                                            </tr>
                                          </thead>
                                          <tbody v-if="data.length">
                                            <tr
                                              v-for="(item,index) in data"
                                              :key="index"
                                              @click="setform(item)"
                                            >
                                              <td>{{index+1}}</td>
                                              <td>{{ item.names }}</td>
                                              <td>{{ item.qty - item.used_qty}}</td>
                                              <!-- <td>{{ item.batch }}</td> -->
                                              <!-- <td>{{ item.used_qty }}</td> -->
                                              <td>{{item.puv}}</td>
                                              <td>{{ item.discount }}</td>
                                              <td>{{item.created_at}}</td>
                                            </tr>
                                          </tbody>
                                          <tbody v-else>
                                            <tr>
                                              <td colspan="6">
                                                <span><v-progress-linear indeterminate :value="40" color="primary" ></v-progress-linear></span>
                                              </td>
                                            </tr>
                                          </tbody>
                                        </template>
                                      </v-simple-table>
                            </v-flex>
            <!-- <v-simple-table dense fixed-header>
              <template v-slot:default>
                <thead>
                  <th>#</th>
                  <th>Produit</th>
                  <th>Batch N</th>
                  <th>Quantite</th>
                  <th>Prix de Vente</th>
                  <th>Discount</th>
                  <th>Origine</th>
                </thead>
                <tbody>
                  <tr v-for="(item,index) in data" :key="index">
                    <td>{{index+1}}</td>
                    <td>{{item.names}}</td>
                    <td>{{item.batch}}</td>
                    <td>{{item.quantity}}</td>
                    <td>{{item.puv}}</td>
                    <td>{{item.discount}}</td>
                    <td>{{item.country}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table> -->
          </v-card-text>
        </v-card>
    </v-dialog>
    <v-flex sm6 xs12 md6 lg8 pl-1>
        <v-card class="invoice-box">
        <v-card-text>
            <div id="printSheet">
              <table cellpadding="0" cellspacing="0">
                <tr>
                  <td colspan="4">
                    <table style="align:center;">
                      <tr>
                        <td>Ets PAMASA
                          <br>PHARMACY AGROVET <span class="text-uppercase">{{shop.names}}</span>
                            <br>CD/GOM/16-A-1128 <br>Av. {{shop.address}}/Butembo, Nord-kivu <br>Tel: +243991471379, +243990358342
                            <br> <a href="maito:info@etspamasa.com"><span> info@etspamasa.com</span></a>
                        </td>

                        <td>
                          <form>
                            <v-btn
                              x-small
                              color="success"
                              dark
                              text
                              @click.stop="dialog = true"
                            >
                              Ajouter le Client
                            </v-btn><br>
                              Noms: <input type="text" style="text-decoration: underline;" disabled v-model="client.names" placeholder="...................................."/>
                            <br>
                              Address: <input type="text" style="text-decoration: underline;"  disabled v-model="client.address" placeholder="...................................."/>
                              <br> Tele: <input type="text"  style="text-decoration: underline;" disabled v-model="client.phone" placeholder="......................................." />
                              <br>Email: <input type="text"  style="text-decoration: underline;" disabled v-model="client.email" placeholder="...................................." />
                          </form>
                        </td>
                      </tr>
                      <hr>
                    </table>
                  </td>
                </tr>

                <tr class="information" style="margin-top:-300px;">
                  <td colspan="4">
                    <table>
                      <tr>
                        <td style="padding-top:-50px;">
                          Facture#: {{invoiceId}}
                        </td>
                        <td>
                            Date: {{date}}
                        </td>
                      </tr>
                    </table>
                  </td>
                </tr>

                <tr class="heading">
                  <td colspan="3">Mode de payement</td>
                  <td>Cash #</td>
                </tr>

                <tr class="details">
                  <td colspan="3">Cash</td>
                  <td>{{ total | currency }}</td>
                </tr>

                <tr class="heading">
                  <td><span>#</span> Item</td>
                  <td>Qte</td>
                  <td>PU</td>
                  <td>PT</td>
                </tr>

                <tr class="" id="tbody" v-for="(item,index) in itemsc" :key="index">
                  <td><span>{{index+1}} </span><input type="text" :disabled="disabledinvoice"   v-model="item.produit" /><span>
                   <v-btn icon x-small color="red" outlined @click="removeItem(index,item.sheetid,item.quantity)">
                     <v-avatar>
                       <v-icon>mdi-close</v-icon>
                     </v-avatar>
                   </v-btn>
                  </span></td>
                  <td>{{item.quantity}}</td>
                  <td>${{item.amount}}</td>
                  <td>${{item.amount * item.quantity | currency }}</td>

                </tr>

                <tr>
                  <td colspan="4">
                    <button class="btn-add-row"></button>
                  </td>
                </tr>

                <tr class="total">
                  <td colspan="3"></td>
                  <td>Total: ${{total | currency }}</td>
                </tr>
                <tr class="heading">
                  <td>
                       <small><i>La marchandise vendue n'est peut etre ni remise ni echange</i></small>
                  </td>
                </tr>
                <br>
                <div id="printButton">
                  <tr>
                    <v-btn x-small color="error" :loading="loadingannuler" @click="annulerinvoice">Annuler</v-btn>
                    <v-btn x-small @click="proforma" :loading="loadingproforma" color="warning">Proformat</v-btn>
                    <v-btn x-small color="#1D8A69" :loading="loadinginvoice" @click="saveInvoice()">Enregister</v-btn>
                    <!-- <v-btn x-small @click="print" color="default">Print</v-btn> -->
                  </tr>
                </div>
              </table>
            </div>
          </v-card-text> 
        </v-card>
    <v-dialog
      v-model="dialog"
      max-width="300px"
      persistent
    >
      <v-card>
        <v-card-title class="text-h5 white--text" style="background:#1D8A69">
          Formulaire du client
        </v-card-title>
        <v-divider></v-divider>
        <br>
        <v-card-text>
              <v-autocomplete
                v-model="clientnames"
                :loading="loading"
                item-text="names" 
                item-value="id" 
                :items="clientList" 
                cache-items
                label="Recherche.."  
                dense
                item-color="red acccent4"
                @change="getClient(clientnames)"
                hide-no-data 
                prepend-icon="mdi-database-search"
                hide-details
                auto-select-first
              ></v-autocomplete>
                    <br>
                <v-row>
                <v-col
                    cols="12"
                    md="12"
                >
                    <v-text-field
                    v-model="client.names"
                    :counter="50"
                    dense
                    label="Noms Complet du client"
                    required
                    ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    md="6"
                >
                    <v-text-field
                    v-model="client.email"
                    :counter="50"
                    dense
                    label="Email address"
                    ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    md="6"
                >
                    <v-text-field
                    v-model="client.phone"
                    :counter="10"
                    dense
                    type="number"
                    label="Telephone"
                    required
                    ></v-text-field>
                </v-col>

                <v-col
                    cols="12"
                    md="12"
                >
                    <v-text-field
                    v-model="client.address"
                    :counter="10"
                    type="text"
                    dense
                    label="Addresse du client"
                    ></v-text-field>
                </v-col>
                </v-row>

        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="error darken-1"
            text
            @click="clean"
          >
            Annuler
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            :loading="loadingclient"
            @click="addClient()"
          >
            Ajouter
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-flex>
  </v-layout>
</template>
<script>
  import { required, digits, email, max, regex } from 'vee-validate/dist/rules'
  import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

  setInteractionMode('eager')

  extend('digits', {
    ...digits,
    message: '{_field_} needs to be {length} digits. ({_value_})',
  })

  extend('required', {
    ...required,
    message: '{_field_} can not be empty',
  })

  extend('max', {
    ...max,
    message: '{_field_} may not be greater than {length} characters',
  })

  extend('regex', {
    ...regex,
    message: '{_field_} {_value_} does not match {regex}',
  })

  extend('email', {
    ...email,
    message: 'Email must be valid',
  })
  
  export default {
    name:'invoice',
    components:{
    ValidationProvider,
    ValidationObserver
    },
  data(){
    return {
        dialog:false,
        disabled : true,
        disabledinvoice: true,
        clientList: [],
        alert:false,
        loading: false,
        loadingproforma:false,
        loadingclient:false,
        loadingannuler:false,
        loadinginvoice:false,
        loadingdetails:false,
        products:[],
        items: [],
        client:{phone:'',names:'',email:'',address:''},
        itemsc: [],
        error:false,
        msg:'',
        address: '',
        formitems:{id:'',productid:'',quantity:'',batch:'',puv:'',discount:'',names:'',category:'',qty:'',pv:''},
        phone: '',
        dialogItem:false,
        shop:{names:'',address:''},
        email:'',
        names:'',
        data:[],
        invoiceId: null,
        search: null,
        select: null,
        clientnames:'',
        clientId: null,
        category_prod: null,
        category_items: ["Veterinaire", "Agriculture", "Materiel"],
        valid: false,
        name:'',
        productLst:[{'names':'names','values':'id'}]
      }
    },
    watch: {
      search (val) {
        val && val !== this.productLst && this.querySelections(val)
      },
    },
  computed: {
    total() {
      return this.itemsc.reduce(
        (acc, item) => acc + item.amount * item.quantity,
        0
      )
    },
    date(){
      let date = new Date()
      let time = date.getFullYear() +"/"+date.getMonth()+1 +"/"+date.getDate()+" "+date.getHours()+":"+date.getMinutes()+":"+date.getSeconds()
      return time
    }
  },
  methods: {
    addRow() {
      var row=""
      row=localStorage.getItem("records")
      this.itemsc=JSON.parse(row)
      // this.invoiceId = localStorage.getItem('inId')
    },
    editinvoice(){
    this.disabledinvoice = false
    },
    submit () {
        this.$refs.observer.validate()
      },
      clean(){
          this.client.names = ""
          this.client.email = ""
          this.client.phone = ""
          this.client.address = ""
          this.clientId = ""
          this.dialog = false
      },
    clear() {
      this.formitems={id:'',productid:'',quantity:'',batch:'',puv:'',discount:'',names:'',category:'',qty:'',pv:''}
      this.$refs.observer.reset()
      this.select = ''
      this.clientnames=''
    },
      querySelections (v) {
        this.loading = true
        // Simulated ajax query
        setTimeout(() => {
          this.items = this.items.filter(e => {
            return (e || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
          })
          this.loading = false
        }, 500)
      },
      setform(items){
        this.formitems.id = items.id
        this.formitems.names = items.names
        this.formitems.productid = items.productid
        this.dialogItem = false
        this.disabled=true
        this.formitems.qty =1
        this.formitems.quantity = items.qty-items.used_qty
        this.formitems.puv=items.puv
        this.formitems.pv = items.puv
        this.formitems.category = items.category
        this.formitems.discount = items.discount
        this.data =[]

      },
      getClient(id){
        this.loadingclient=true
         this.$api.post('getclientId/'+id)
         .then(rs=>{
            this.client.names = rs.data.names
            this.client.email = rs.data.email
            this.client.phone = rs.data.telephone
            this.client.address = rs.data.address
            this.clientId = rs.data.id
            this.loadingclient=false
        })
    },
    removeItem(index,item,quantity){
      this.itemsc.splice(index,1)
      this.clear()
      this.$api.post('removeSingleItem/'+item+'/'+quantity)
          .then(rs=>{
            if(rs.data.success){
              this.msg = "Le produit a ete supprime avec success."
              this.alert = true
              setTimeout(()=>{
                this.alert=false
              },5000)
            }
            else{
              this.msg = "Vous ne pouvez effectuez cet operation."
              this.error = true
              setTimeout(()=>{
                this.error=false
              },5000)
            }
          })
    },
    addClient(){
        this.loadingclient=true
      if(this.clientId==null){
        const fm = new FormData()
        // fm.append('id',this.clientId)
        fm.append('names',this.client.names)
        fm.append('email',this.client.email)
        fm.append('phone',this.client.phone)
        fm.append('address',this.client.address)
        fm.append('branche',this.$store.state.user.brancheid)
        fm.append('token',this.$store.state.user.token)
        this.$api.post('invoice_client',fm)
        .then(rs=>{
          const id = rs.data.client
          if(id !==""){
            this.clientId=rs.data.client
            this.dialog = false
            // this.clean()
            this.getClients()
            this.loadingclient=false
          }else{
            this.msgAlert=rs.data.success
            this.dialog = false
            this.loadingclient=false
          }
        })
        .catch(rs=>{
          this.loadingclient=false
          this.msg=rs.data.error
          this.error = true
          setTimeout(()=>{
            this.error=false
          },5000)
        })
      }else{
        this.clientId =this.clientId
        this.dialog = false
        this.loadingclient=false
      }
    },
    allItems(){
      let id = this.$store.state.user.brancheid
      this.$api.get('sellings/'+id)
      .then(rs=>{
          this.products = rs.data
      })
      .catch(rs=>{
        this.msg=rs.data.error
        this.error = true
        setTimeout(()=>{
          this.error=false
        },5000)
      })
  },
  findshop(){
    this.$api.get('shopnames/'+this.$store.state.user.brancheid)
    .then(rs=>{
      this.shop= rs.data
      localStorage.setItem('branche',JSON.stringify(this.shop))
    })
  },
    print(){
      this.router.push('/print')
    },
      shproducts(){
      let i = this.$store.state.user.brancheid
      this.$api.get('sellings/'+i)
      .then(rs=>{
        this.items = rs.data
      })
      .catch(rs=>{
        this.msg=rs.data.error
        this.error = true
        setTimeout(()=>{
          this.error=false
        },5000)
      })
    },
    clients(){
      let id = this.$store.state.user.brancheid
      this.$api.post('clients/'+id)
      .then(rs=>{
        this.clientList = rs.data.clients
      })
      .catch(rs=>{
        this.msg=rs.data.error
        this.error = true
        setTimeout(()=>{
          this.error=false
        },5000)
      })
    },
    submitvente(){
      this.loading=true
      let available = this.formitems.qty <= this.formitems.quantity
      if(this.formitems =='' || available==false){
        this.msg = "Vous ne pouvez pas effectuer cette operation"
        this.error = true
        this.loading=false
        setTimeout(()=>{
        this.error=false
        },5000)
      }
      else{
        let price = this.formitems.puv-this.formitems.discount
        if((this.formitems.pv)>=price){
          let invoice ={
            sheetid: this.formitems.id,
            productid:this.formitems.productid,
            amount: this.formitems.pv,
            clientId: this.clientId,
            quantity: this.formitems.qty,
            invoiceId:this.invoiceId,
            produit: this.formitems.names

          }
          this.itemsc.push(invoice)
          this.msg= "Le produit a ete ajoute a la feuille de facture avec success"
          this.alert = true
          setTimeout(()=>{
            this.alert=false
          },5000)
          this.$store.commit('addToInvoice',JSON.stringify(this.itemsc))
          const form = new FormData()
          form.append('sheetid', this.formitems.id)
          form.append('price', this.formitems.pv)
          form.append('quantite', this.formitems.qty)
          this.$api.post('checkInvoiceItem',form)
          .then(rs=>{
            this.clear()
            this.formitems.quantity=rs.data.qtyd
            this.loading=false 
          })
          .catch(rs=>{
            this.loading=false
            this.msg=rs.data.error
            this.error = true
            setTimeout(()=>{
              this.error=false
            },5000)
          })
        }else
        {
          let courrent = this.formitems.puv-this.formitems.discount
          this.formitems.pv = this.formitems.puv
          this.msg = "Desolé ! Le prix de reduction ne doit pas depasser "+ courrent + "pour ce produit."
          this.error = true
          this.loading=false
          setTimeout(()=>{
            this.error=false
          },5000)
        }
      }
    },
    annulerinvoice(){
      this.loadingannuler=true
      if(this.itemsc ==''){
        this.msg = "La feuille est vide. Vous ne pouvez pas effectuer cette operation."
        this.error = true
        this.loadingannuler=false
        setTimeout(()=>{
        this.error=false
        },5000)
      }else{
        this.msg= "L'opertation a reussit avec success."
          this.alert = true
          setTimeout(()=>{
            this.alert=false
          },5000)
        let form = new FormData()
        form.append('items',JSON.stringify(this.itemsc))
        localStorage.removeItem('records')
        localStorage.removeItem('client')
        this.$api.post('removeinvoiceItem',form)
        .then(()=>{
          this.itemsc=[]
          this.clean()
          this.clear()
          // rs.data
          this.loadingannuler=false
        })
        .catch(rs=>{
        this.msg=rs.data.error
        this.error = true
        setTimeout(()=>{
          this.error=false
        },5000)
      })
      }
    },
    proforma(){
      this.loadingproforma=true
      if(this.itemsc ==''){
        this.loadingproforma=false
        this.msg = "La feuille est vide. Vous ne pouvez pas effectuer cette operation."
        this.error = true
        setTimeout(()=>{
        this.error=false
        },5000)
      }else{
        let form = new FormData()
        form.append('items',JSON.stringify(this.itemsc))
        this.$api.post('removeinvoiceItem',form)
        .then(()=>{
          this.$router.push('/print')
          this.itemsc=[]
          this.clean()
          this.clear()
          this.loadingproforma=false
          // rs.data
        })
        .catch(rs=>{
          this.loadingproforma=false
          this.msg=rs.data.error
          this.error = true
          setTimeout(()=>{
            this.error=false
          },5000)
        })
      }
    },
    get_details(index){
      this.loadingdetails = true
      let i = this.$store.state.user.brancheid
      let form = new FormData()
      form.append('branche',i)
      form.append('item',index)
          this.$api.post('item_info_branche',form)
          .then(rs=>{
            this.loadingdetails=false
            this.data = rs.data
          })
        },
    saveInvoice(){
      this.loadinginvoice =true
      if(this.itemsc ==''){
        this.msg = "La feuille est vide. Vous ne pouvez pas effectuer cette operation."
        this.error = true
        this.loadinginvoice =false
        setTimeout(()=>{
        this.error=false
        },5000)
      }else{
        if(this.clientId ==null){
            this.clientId = 1
        }

        localStorage.setItem('client',JSON.stringify(this.client))
        const fm = new FormData()
        fm.append('items',JSON.stringify(this.itemsc))
        fm.append('clientId',this.clientId)
        fm.append('token',this.$store.state.user.token)
        fm.append('branche',this.$store.state.user.brancheid)
        this.$api.post('saveInvoice',fm)
        .then(rs=>{
          localStorage.setItem('invoice',rs.data.invoice)
          this.$store.dispatch('isPrinting')
          this.itemsc.splice(this.itemsc)
          this.itemsc=[]
          this.$router.push('/print')
          this.invoiceId=rs.data.invoice
          this.clear()
          this.clean()
          this.loadinginvoice =false
        })
      }
    }
  },
  mounted(){
    if(localStorage.getItem('records') !== null){
      this.addRow()
    }
    this.findshop()
    this.allItems()
    this.shproducts()
    this.clients()
    document.title="Facture | Shop.namesharma"
  },
  filters: {
    currency(value) {
      return value.toFixed(2);
    }
  },
}

</script>

