<template>
    <v-app>
        <v-subheader light>
            <h2>Fournisseurs <span style='color:red'></span></h2>
            <v-spacer></v-spacer>
            <small>Fournisseurs | <small>Ets Pamasa</small></small>
        </v-subheader>
        <br>

        <v-alert text tile :value="alert" dismissible border="left" :type="type" dense>{{msg}}</v-alert>
        <v-alert text tile :value="alert" dismissible border="left" :type="type" dense>{{msg}}</v-alert>
        <v-card  tile elevation="2">
            <v-card-title>
                <span>Fournisseurs</span>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-row>
                    <v-col cols="12" md="4">
                        <v-card>
                            <v-card-title style="background: #1D8A69" class="white--text">
                                <span>{{(newvendor.id ? "Modification du Fournisseur" : "Formulaire du Fournisseur")}}</span>
                            </v-card-title>
                            <v-divider></v-divider>
                            <v-card-text>
                                <v-text-field v-model="newvendor.names" label="Noms du Fournisseur"></v-text-field>
                                <v-text-field v-model="newvendor.country" label="Pays du Fournisseur"></v-text-field>
                                <v-text-field v-model="newvendor.city" label="Ville du Fournisseur"></v-text-field>
                                <v-text-field v-model="newvendor.phone" label="Telephone du Fournisseur"></v-text-field>
                                <v-text-field v-model="newvendor.email" label="Email addresse du Fournisseur"></v-text-field>
                                <v-select v-model="newvendor.status" :items="status" label="Statut du Fournisseur"></v-select>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-btn color="default" @click="clean">Annuler</v-btn>
                                <v-spacer></v-spacer>
                                <v-btn color="#1D8A69" class="white--text" @click="savevendor" :loading="loading">
                                    <span >{{(newvendor.id ? "Modifier" : "Enregistrer")}}</span>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-col>
                    <v-col cols="12" md="8">
                        <v-card>
                            <v-card-title>
                                <span>Liste des Fournisseurs</span>
                                <v-spacer></v-spacer>
                                <v-text-field 
                                v-model="search"
                                append-icon="mdi-magnify"
                                label="Recherche"
                                single-line
                                hide-details>
                            </v-text-field>
                            </v-card-title>
                            <v-divider></v-divider>
                            <v-card-text>
                                <v-data-table
                                dense
                                :headers="headers"
                                :items="vendors"
                                :items-per-page="10"
                                :search="search"
                                class="elevation-1"
                                >
                                    <template v-slot:body="{items}">
                                        <tbody>
                                            <tr v-for="(vendor,index) in items" :key="index">
                                                <td>{{index+1}}</td>
                                                <td>{{vendor.names}}</td>
                                                <td>{{vendor.city}}</td>
                                                <td>{{vendor.country}}</td>
                                                <td>{{vendor.phone}}</td>
                                                <td>{{vendor.email}}</td>
                                                <td>{{vendor.status}}</td>
                                                <td>
                                                    <span>
                                                        <v-btn x-small color="#1D8A69" class="white--text" @click="setvendor(vendor)">
                                                            <v-icon x-small>mdi-pencil</v-icon>
                                                            <span>Modifier</span>
                                                        </v-btn>
                                                        <!-- <v-btn x-small color="error" @click="deletevendor(vendor.id)">
                                                            <span>Sup</span>
                                                        </v-btn> -->
                                                    </span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </template>
                                </v-data-table>
                            </v-card-text>
                        </v-card>
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
    </v-app>
</template>
<script>
export default {
    data(){
        return{
            loading:false,
            newvendor:{names:'',country:'',email:'',phone:'',city:'',id:'',status:''},
            headers:[{text:'#',value:'id'},{text:'Noms',value:'names'},
                {text:'Ville',value:'city'},{text:'Country',value:'country'},
                {text:'phone',value:'phone'},{text:'email',value:'email'},
                {text:'Status',value:'status'},{text:'Actions',value:''}
                ],
            vendors:[],
            status:['Active','Desactive'],
            search:null,
            alert: false,
            type:null,
            msg:'',
            error:false,
        }
    },
    methods:{
        clean()
        {
            this.newvendor={names:'',country:'',email:'',phone:'',city:'',id:''}
        },
        savevendor(){
            this.loading=true
            let form  = new FormData()
            form.append('names',this.newvendor.names)
            form.append('country',this.newvendor.country)
            form.append('email',this.newvendor.email)
            form.append('phone',this.newvendor.phone)
            form.append('city',this.newvendor.city)
            form.append('id',this.newvendor.id)
            form.append('status',this.newvendor.status)
            form.append('token',this.$store.state.user.token)
            this.$api.post('/manipulate_vendor',form)
            .then(rs=>{
                if(rs.data.success){
                    this.loading=false
                    this.msg=rs.data.success
                    this.alert = true
                    setTimeout(()=>{
                        this.alert=false
                    },3000)
                    this.getvendors()
                    this.clean()
                }
                else{
                    this.msg=rs.data.error
                    this.loading=false
                    this.alert = true
                    setTimeout(()=>{
                    this.alert=false
                    },3000)
                } 
                })
            .catch(rs=>{
                this.loading=false
                this.msg=rs.data.error
                this.error = true
                setTimeout(()=>{
                this.error=false
                },5000)
            })
        },
        getvendors(){
            this.$api.get('vendors')
            .then(rs=>{
                this.vendors = rs.data
            })
        },
        setvendor(vendor){
            this.newvendor = vendor
        },
        deletevendor(index){
            this.$api.get('delete_vendor/'+index)
            .then(rs=>{
                if(rs.data.success){
                    this.msg=rs.data.success
                    this.alert = true
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                    this.getvendors()
                    this.clean()
                }
                else{
                    this.msg=rs.data.error
                    this.error = true
                    setTimeout(()=>{
                    this.error=false
                    },5000)
                } 
                })
            .catch(rs=>{
                this.msg=rs.data.error
                this.error = true
                setTimeout(()=>{
                this.error=false
                },5000)
            })
        }
    },
    mounted(){
        this.getvendors()
        document.title="Fournisseur | ETSPAMASA"
    }
}
</script>