<template>
    <v-app>
        <v-card tile>
            <v-card-text>
                <v-card tile>
                    <v-toolbar dense>List de Branches
                        <v-spacer></v-spacer>
                        <v-alert dense :type="type" :value="alert" v-html="msg"></v-alert>
                    </v-toolbar>
                    <v-card-text>
                        <v-flex>
                            <v-row>
                                <v-col cols="12" md="4">
                                    <v-card>
                                        <v-card-title style="background-color:#1D8A69;" class="white--text">
                                            Formulaire
                                        </v-card-title>
                                        <v-divider></v-divider>
                                        <form>
                                            <v-card-text>
                                                <v-text-field v-model="branche.names" label="Nom du Branche" dense outlined></v-text-field>
                                                <v-text-field v-model="branche.address" label="Addresse du Branche" dense outlined></v-text-field>
                                                <v-text-field v-model="branche.chef" label="Chef du Branche" dense outlined></v-text-field>
                                                <v-text-field v-model="branche.status" label="Status du Branche" dense outlined></v-text-field>
                                            </v-card-text>
                                            <v-divider></v-divider>
                                            <v-card-actions>
                                                <v-btn small @click="annuler">Annuler</v-btn>
                                                <v-spacer></v-spacer>
                                                <v-btn small color="#1D8A69" class="white--text" :loading="loading" @click="manipulate_branche">Ajouter</v-btn>
                                            </v-card-actions>
                                        </form>
                                    </v-card>
                                </v-col>
                                <v-col cols="12" md="8">
                                    <v-card tile>
                                        <v-card-title>
                                           <small>List de Branches</small>
                                            <v-spacer></v-spacer>
                                            <v-text-field 
                                                v-model="search"
                                                append-icon="mdi-magnify"
                                                label="Recherche"
                                                single-line
                                                hide-details>
                                            </v-text-field>
                                        </v-card-title>
                                        <v-divider></v-divider>
                                        <v-card-text>
                                            <v-data-table
                                            :headers="headers"
                                            :items="branches"
                                            loading-text="En cours ... Veuillez Patienter"
                                            :loading="loadingdata"
                                            :items-per-page="10"
                                            :search="search">
                                            <template v-slot:body="{items}">
                                                <tbody v-if="items.length">
                                                    <tr v-for="(branche,index) in items" :key="index">
                                                        <td>{{index+1}}</td>
                                                        <td>{{branche.names}}</td>
                                                        <td>{{branche.address}}</td>
                                                        <td>{{branche.chef}}</td>
                                                        <td>{{branche.status == 1?'Operationnelle':'Desactiver'}}</td>
                                                        <td>
                                                            <span>
                                                                <v-btn x-small color="info" @click="createsheet(branche.id)">Produit</v-btn>
                                                                <v-btn x-small color="error" @click="mod(branche)">Modifier</v-btn>
                                                                <v-btn x-small color="primary" @click="agents(branche.id)">Agents</v-btn>
                                                            </span>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                <tbody v-else>
                                                    <tr>
                                                        <td colspan="6" style="text-align:center;">Pas de Donnees</td>
                                                    </tr>
                                                </tbody>
                                            </template>
                                            </v-data-table>
                                        </v-card-text>
                                    </v-card>
                                </v-col>
                            </v-row>
                        </v-flex>
                    </v-card-text>
                </v-card>
            </v-card-text>
        </v-card>
    </v-app>
</template>
<script>
export default {
    data(){
        return{
            loading:false,
            branche:{names:'',address:'',chef:'',status:'',id:''},
            branches:[],
            loadingdata:false,
            search:null,
            headers:[{text:'#',value:'1'},{text:'Noms',value:'names'},
            {text:'Addresse',value:'address'},{text:'Gerant',value:'chef'},
            {text:'Status',value:'status'},{text:'Actions',value:''}],
            alert:false,
            type:null,
            msg: null,
        }
    },
    methods:{
        createsheet(id){
            // console.log(index)
            this.$router.push('/sheet/'+id)
        },
        agents(i){
            this.$router.push('/agents/'+i)
        },
        annuler(){
            this.branche = {names:'',address:'',chef:'',status:'',id:''}
        },
        getbranche(){
            this.loadingdata = true
            this.$api.get('branches')
            .then(rs=>{
                this.branches = rs.data
                this.loadingdata = false
            })
        },
        manipulate_branche(){
            if(this.branche.names == '' || this.branche.address=='' || this.branche.status ==''){
                this.msg = 'Veuillez Remplire le formulaire.'
                this.alert=true
                this.type ='error'
                setTimeout(()=>{
                    this.alert=false
                },5000)
            }
            else{
                this.loading = true
                let form = new FormData();
                form.append('names',this.branche.names)
                form.append('address',this.branche.address)
                form.append('status',this.branche.status)
                form.append('chef',this.branche.chef)
                form.append('id',this.branche.id)
                this.$api.post('manipulate_branches',form)
                .then(rs=>{
                    if(rs.data.error){
                        this.alert = true
                        this.type='error'
                        this.loading = false
                        this.msg = rs.data.error
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                    }else{
                        this.getbranche()
                        this.alert = true
                        this.type = 'success'
                        this.msg = rs.data.message
                        this.loading = false
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                        this.annuler()
                    }
                })
            }
        },
        mod(index){
            this.branche = index
        }
    },
    mounted(){
        this.getbranche()
        document.title ="Branche | ETSPAMASA"
    }
}
</script>