import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import Product from '../views/Product'
import Vente from '../views/vente'
import Users from '../views/users'
import allVentes from '../views/allvente'
import login from '../views/login'
import report from '../views/rapport'
import supplier from '../views/supplier'
import newproduct  from '../views/new'
import branche from '../views/branche'
import sheet  from '../views/sheet'
// import store from '../store'
import agents from '../views/agents'
import print from '../views/print'
import profile  from '../views/profile'
import archives from '../views/archive'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
    meta:{
      requireLogin:true
    }
    
  },
  {
    path: '/new',
    name: 'Arrivage',
    component: newproduct
  },

  {
    path: '/sheet/:id',
    name: 'Branche Sheet',
    component: sheet
  },

  {
    path: '/agents/:id',
    name: 'Branche Agents',
    component: agents
  },
  {
    path: '/branche',
    name: 'Branches',
    component: branche,
  },
  {
    path:'/archive/:id',
    name:'clientArchive',
    component:archives
  },
  {
    path: '/product',
    name: 'Product',
    component: Product,
    children: [
      {
        path: '/new',
        component: newproduct
      },
      // {
      //   path: 'posts',
      //   component: UserPosts
      // }
    ]
  },
  {
    path: '/vendor',
    name: 'Vendeur',
    component: supplier
  },
  {
    path: '/report',
    name: 'Rapport',
    component: report
  }, 
  {
    path: '/vente',
    name: 'Vente',
    component: Vente,
    meta:{
      requireLogin:true
    }
  },
  {
    path: '/print',
    name: 'print',
    component: print
  },
  {
    path: '/users',
    name: 'Utilisateur',
    component: Users
  },
  {
    path: '/allventes',
    name: 'Ventes',
    component: allVentes
  },
  {
    path:'/profile',
    name:'profile',
    component: profile
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.VUE_APP_APPNAME,
  routes
})
router.beforeEach((to, from, next) => {
  if (to.name !== 'login' && localStorage.getItem('token') === null) next({ name: 'login' })
  else next()
})
// router.beforeEach((to,from,next)=>{
//   if(to.matched.some(record => record.meta.requireLogin) && !store.isLoggedIn==true){
//     next()
//   }else{
//     next()
//   }
// })
export default router
