                              </validation-provider>
                              </v-col>

                                <v-col
                                cols="12"
                                md="2"
                              >
                              <validation-provider
                                v-slot="{ errors }"
                                name="Numero du Facture"
                                class="d-flex"
                                cols="12"
                                sm="4"
                                rules="required|max:500"
                              >
                                <v-text-field
                                  v-model="arrivage.number"
                                  type="number"
                                  :disabled="disabled"
                                  outlined
                                  :error-messages="errors"
                                  label="Numero du Facture"
                                  dense
                                  required
                                ></v-text-field>
                              </validation-provider>
                              </v-col>

                                <v-col
                                cols="12"
                                md="3"
                              >
                              <validation-provider
