                              <v-col cols="12" md="1" v-if="itemsc!==''">
                                    <v-btn x-small color="primary" @click="createArrivage" type="submit" :disabled="invalid">
                                        <span>Proceder</span>
                                    </v-btn>
                                    <v-btn @click="clear" x-small color="error">
                                        Annuler
                                    </v-btn>
                              </v-col>
                            </v-row>
                            <div v-if="newarrivage!==''">
                              <!-- <div> -->
                              <v-card tile dense>
                                <v-toolbar dense tile class="white--text" color="#1D8A69">
                                  <span>Enregistrement des produits de la facture</span>
                                  <v-spacer></v-spacer>
                                  <div v-if="itemsc !==''">
                                    <v-btn x-small color="error" @click="annulerItems(itemsc)">
                                      <span>Annuler</span>
                                    </v-btn>
                                    <v-btn x-small color="primary" @click="saveinvoiceitems">
                                      <span>Enregistrer</span>
                                    </v-btn>
                                  </div>
                                </v-toolbar>
                                <v-card-text>
                                  <v-row>
                                    <v-col cols="12" md="6">

                                          <validation-observer ref="observer" v-slot="{invalid}">
                                            <v-row>
                                                  <v-col class="12" md="5">
                                                    <validation-provider
                                                      v-slot="{ errors }"
                                                      name="Le noms du produit"
                                                      class="d-flex"
                                                      cols="12"
                                                      sm="5"
                                                      rules="required|max:500"
                                                    >
                                                      <v-autocomplete
                                                        v-model="item.product"
                                                        outlined
