                                                        :error-messages="errors"
                                                        label="Produit"
                                                        item-text="names"
                                                        @change="get_detail(item.product)"
                                                        item-value="id" 
                                                        :search-input.sync="item.names"
                                                        cache-items
                                                        dense
                                                        required
                                                      ></v-autocomplete>
                                                    </validation-provider>
                                                    </v-col>
                                                    <v-col class="12" md="2">
                                                      <validation-provider
                                                      v-slot="{ errors }"
                                                      name="La quantite du produit"
                                                      class="d-flex"
                                                      cols="12"
                                                      sm="5"
                                                      rules="required|max:500"
                                                    >
                                                        <v-text-field v-model="item.quantity" :error-messages="errors" outlined dense label="Qte"></v-text-field>
                                                      </validation-provider>
                                                    </v-col>
                                                    <v-col class="12" md="3">
                                                      <validation-provider
                                                      v-slot="{ errors }"
                                                      name="Le prix d'Achat du produit"
                                                      class="d-flex"
                                                      cols="12"
                                                      sm="5"
                                                      rules="required|max:500"
                                                    >
                                                        <v-text-field v-model="item.price" :error-messages="errors" outlined dense label="PUA"></v-text-field>
                                                      </validation-provider>
                                                    </v-col>
                                                      <v-col class="12" md="3">
                                                        <validation-provider
                                                        v-slot="{ errors }"
                                                        name="Le batch number du produit"
                                                        class="d-flex"
                                                        cols="12"
                                                        sm="5"
                                                        rules="required|max:500"
                                                      >
                                                          <v-text-field v-model="item.batch" :error-messages="errors" outlined dense label="Batch"></v-text-field>
                                                        </validation-provider>
                                                    </v-col>
                                                    <v-col class="12" md="4">
                                                        <validation-provider
                                                        v-slot="{ errors }"
                                                        name="Le prix de vente du produit"
                                                        class="d-flex"
                                                        cols="12"
                                                        sm="5"
                                                        rules="required|max:500"
                                                      >
                                                        <v-text-field v-model="item.amount" :error-messages="errors" outlined dense label="PUV"></v-text-field>
                                                        </validation-provider>
                                                    </v-col>
                                                    <v-col class="12" md="3">
                                                        <v-text-field v-model="item.discount" outlined dense label="Reduction"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="12" md="6">
                                                            <v-btn color="default" small @click="saveinfo">
                                                                <span>Annuler</span>
                                                            </v-btn>
                                                    </v-col>
                                                    <v-col cols="12" md="6">
                                                            <v-btn color="primary" x-small @click="saveinfo" :disabled="invalid">
                                                                <span>Ajouter</span>
                                                            </v-btn>
                                                    </v-col>
                                            </v-row>
                                          </validation-observer>
                                    </v-col>
                                    <v-col cols="12" md="6">
                                      <v-simple-table dense fixed-header>
                                        <template v-slot:default>
                                          <thead>
                                            <tr>
                                              <th></th>
                                              <th>N#</th>
                                              <th class="text-left">
                                                Product
                                              </th>
                                              <th>Qte</th>
                                              <th>Batch</th>
                                              <th>PA</th>
                                              <th>PV</th>
                                              <th>Discount</th>
                                              <th>PTA</th>
                                            </tr>
                                          </thead>
                                          <tbody>
                                            <tr
                                              v-for="(item,index) in itemsc"
                                              :key="index"
                                            >
                                              <td>
                                                  <v-btn icon x-small color="red" outlined @click="removefield(index)">
                                                    <v-icon>mdi-close</v-icon>
                                                  </v-btn>
                                              </td>
                                              <td>{{index+1}}</td>
                                              <td>{{ item.names }}</td>
                                              <td>{{ item.quantity }}</td>
                                              <td>{{ item.batch }}</td>
                                              <td>${{ item.pua }}</td>
                                              <td>${{item.puv}}</td>
                                              <td>${{ item.discount }}</td>
                                              <td>${{ item.quantity*item.pua }}</td>
                                            </tr>
                                          </tbody>
                                        </template>
                                      </v-simple-table>
                                    </v-col>
                                  </v-row>
                                </v-card-text>
                              </v-card>
                            </div>
                            </form>
                      </validation-observer>
                    </v-card-text>
                </v-card>
                <br>
                <v-card>
                  <v-toolbar tile dense>
                    <span>Tous les achats</span>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="find"
                      append-icon="mdi-magnify"
                      label="Recherche..."
                      dense
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                  <v-flex ml-2 mt-2 mr-2>
                    <v-row>
                      <v-col cols="12" md="5">
                        <v-data-table
                          dense
                          :headers="headers"
                          :items="arrivages"
                          :items-per-page="10"
                          :search="find"
                          >
                            <template v-slot:body="{items}">
                              <!-- <td colspan="5">
                                <v-container>
                                </v-container>
                              </td> -->
                              <tr v-for="(item,index) in items" :key="index">
                                <td>{{index+1}}</td>
                                <td>{{item.names}}</td>
                                <td>{{"00"+item.id}}</td>
                                <td>{{item.amount}}</td>
                                <td>{{item.created_at}}</td>
                                <td>
                                  <span>
                                    <v-btn x-small color="primary" @click="viewarrivage(item.id)">
                                      <v-icon>mdi-eye</v-icon><span>View</span>
                                    </v-btn>
                                  </span>
                                </td>
                              </tr>
                            </template>
                        </v-data-table>
                      </v-col>
                      <v-col md="7" cols="12">
                        <v-flex ml-2>
                          <v-simple-table dense fixed-header>
                                        <template v-slot:default>
                                          <thead>
                                            <tr>
                                              <th></th>
                                              <th>#</th>
                                              <th class="text-left">
                                                Item(Produit)
                                              </th>
                                              <th>Qte</th>
                                              <th>Batch</th>
                                              <th>PA</th>
                                              <th>PV</th>
                                              <th>Discount</th>
                                              <th>PTA</th>
                                            </tr>
                                          </thead>
                                          <tbody>
                                            <tr
                                              v-for="(item,index) in data"
                                              :key="index"
                                            >
                                              <td>
                                                <span>
                                                  <v-btn icon x-small color="primary" outlined @click="editfield(item.id)">
                                                    <v-icon>mdi-pencil</v-icon>
                                                  </v-btn>
                                                  <!-- <v-btn icon x-small color="red" outlined @click="editfield(item.id)">
                                                    <v-icon>mdi-close</v-icon>
                                                  </v-btn> -->
                                                </span>
                                              </td>
                                              <td>{{index+1}}</td>
                                              <td>{{ item.names }}</td>
                                              <td>{{ item.quantity }}</td>
                                              <td>{{ item.batch }}</td>
                                              <td>${{ item.pua }}</td>
                                              <td>${{item.puv}}</td>
                                              <td>${{ item.discount }}</td>
                                              <td>${{ item.quantity*item.pua }}</td>
                                            </tr>
                                          </tbody>
                                        </template>
                                      </v-simple-table>
                            </v-flex>
