            </v-card-text>
        </v-card>
    </v-app>
</template>
<script>
    import { required, digits,max, regex } from "vee-validate/dist/rules";
    import {
        extend,
        ValidationObserver,
        ValidationProvider,
        setInteractionMode,
    } from "vee-validate";

    setInteractionMode("eager");

    extend("digits", {
    ...digits,
    message: "{_field_} needs to be {length} digits. ({_value_})",
    });

    extend("required", {
    ...required,
    message: "{_field_} ne peut pas etre vide",
    });

    extend("max", {
    ...max,
    message: "{_field_} ne peut pas depasser {length} characters",
    });

    extend("regex", {
    ...regex,
    message: "{_field_} {_value_} ne correspond pas {regex}",
    });


    export default {
    components: {
        ValidationProvider,
        ValidationObserver,
    },
    data: () => ({
        disabled:false,
        post : [],
        data:[],
        msgAlert: null,
        dialog: false,
        newarrivage:'',
        addeditems:[],
        find:'',
        arrivages:[],
        headers:[{text:'#',value:'id',filterable: false,},{text:'Fournisseur',value:'names'},{text:'Code',value:'id'},{text:'Montant',value:'amount'},{text:'SavedOn',value:'created_at'},{text:''}],
        arrivage: {value:"",date:"",vendor:"",number:""},
        vendors: [],
        products:[],
        itemsc:[],
        item:{product:'',names:'',discount:'',quantity:'',batch:'',amount:'',price:'',id:''},
        alert:false,
        msg:null,
    }),

    methods: {
      resetform(){
        this.item = {product:'',names:'',discount:'',quantity:'',batch:'',amount:'',price:'',id:''}
        this.newarrivage =''
        this.disabled = false
      },
        submit () {
            this.$refs.observer.validate()
        },
        clear() {
        this.arrivage= {value:"",date:"",vendor:"",number:""}
        this.newarrivage =''
        this.disabled = false
        this.$refs.observer.reset()
        this.annulerItems()
        },
        createArrivage(){
            let form = new FormData()
            form.append('value',this.arrivage.value)
            form.append('number',this.arrivage.number)
            form.append('date',this.arrivage.date)
            form.append('vendor',this.arrivage.vendor)
            form.append('token',this.$store.state.user.token)
            this.$api.post('manipulate_arrivage',form)
            .then(rs=>{
                this.alert = true
                this.disabled=true
                this.newarrivage = rs.data.arrivage
                this.msg =rs.data[0].message
            })
        },
      removefield(index){
          this.resetform()
          var row =index
         this.itemsc.splice(row,1)
      },
        saveinfo(){
            let item = {
              product: this.item.product,
              names: this.item.names,
              quantity: this.item.quantity,
              batch: this.item.batch,
              discount: this.item.discount,
              pua: this.item.price,
              puv: this.item.amount
            }
            this.itemsc.push(item)
            this.$store.dispatch('arrivage_items',this.itemsc)
        },
        editfield(edit){
          edit
        },
        get_detail(index){
          this.$api.get('product_info/'+index)
          .then(rs=>{
            this.item.names = rs.data[0].names
          })
        },
        annulerItems(items){
          localStorage.removeItem('arrivage')
          this.itemsc.splice(items)
          this.itemsc=[]
        },
        saveinvoiceitems(){
          let form  = new FormData()
          form.append('items',JSON.stringify(this.itemsc))
          form.append('arrivage',this.newarrivage)
          this.$api.post('manipulate_arrivageinfo',form)
          .then(rs=>{
            localStorage.removeItem('arrivage')
            this.alert = true
            this.disabled=true
            this.msg =rs.data.message
            this.itemsc.splice(this.itemsc)
            this.itemsc=[]
            this.resetform()
            this.clear()
            this.getArrivages()
            setTimeout(()=>{
                this.alert=false
            },5000)
          })
        },
        viewarrivage(view){
          this.$api.get('arrivages_info/'+view)
          .then(rs=>{
            this.data = rs.data
          })
        },
        getArrivages(){
          this.$api.get('arrivages')
          .then(rs=>{
            this.arrivages= rs.data
          })
        },
        getvendors(){
            this.$api.get('vendors')
            .then(rs=>{
                this.vendors = rs.data
            })
        },
        getproducts(){
            this.$api.get('items')
            .then(rs=>{
              this.products = rs.data
            })
        },
    },
    mounted(){
      this.getArrivages()
        this.getvendors()
        this.getproducts()
        document.title="Arrivage | ETSPAMASA"
    },
    
    }
</script>
