import Vue from "vue";
import Vuex from 'vuex'
import axios from '../plugins/axios.js'
import router from "../router"

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        invoices: [],
        items:[],
        invoice_data:[],
        user:[],
        isLoggedIn:false,
        names:'Raha Katumbi',
    },
    getters:{
        isLoadingState: (state) => {
            return state.isLoggedIn
        },
        getInvoices: (state) =>{
            return state.invoices
        },
        user:(state)=>{
            return state.user
        },
        arrivage_data: (state) => {
            return state.items
        },
        current_data:(state)=>{
            return state.invoice_data
        },
    },
    actions:{
        currentsInvoices({commit}){
            axios.api.get('/allventes')
            .then(res=>{
                commit('set_invoices',res.data)
            })
        },
        arrivage_items({commit},items){
            commit('set_items',items)
        },
        initializeItems({commit}){
            commit('initializeItems')
        },
        isLoggin({commit},item){
            commit('login',item),
            commit('initializelogs')
        },
        initializeStore({commit}){
            commit('initializelogs')
        },
        isLogout({commit}){
            commit('logout')
        },
        current_data({commit},data){
            commit('current_data',data)
        },
        isPrinting({commit}){
            commit('isPrinting')
        }
    },
    mutations:{
        login(state,user) {
            state.isLoggedIn = true
            state.user = user
            localStorage.setItem('token',JSON.stringify(user))
            // router.push('/')
            if(router.currentRoute.name !== "Dashboard" && router.currentRoute.name !== "Vente") {
                if(state.user.priviledge ==1){router.push({name : 'Dashboard'}).catch()}
                else {router.push({ path : '/vente'}).catch()}
            }
          },
          logout(state) {
            state.isLoggedIn = false;
            localStorage.removeItem('token')
            state.user =[]
            router.push('/login').catch()
            
          },
        set_invoices(state,invoices){
            state.invoices=invoices
        },
        set_items(state,items){
            state.items = items
            localStorage.setItem('arrivage',JSON.stringify(state.items)) 
            state.items = JSON.parse(localStorage.getItem('arrivage'))
        },
        initializeItems(state){

            if(localStorage.getItem('arrivage')){
                state.items = JSON.stringify(localStorage.getItem('arrivage'))
            }
            else{
                localStorage.setItem('arrivage',JSON.stringify(state.items))
            }
        },
        initializelogs(state){
            if(state.user==null || router.currentRoute.name =='login'){
               localStorage.removeItem("token")
               router.push('/login') 
           }else if(state.user!==null){
            //    let routername = router.currentRoute.name
               state.user =  JSON.parse(localStorage.getItem('token'))
               state.isLoggedIn=true
            //    router.push({name:routername}).catch()
           }
           else{
               router.push({name:'login'}).catch()
           }
       },
       addToInvoice(state,item){
            state.cart=JSON.parse(item)
            localStorage.setItem('records',JSON.stringify(state.cart))
       },
       isPrinting(state){
           // state.isLoggedIn = false
           if(state.isLoggedIn == true){state.isLoggedIn = false}
           else {
               if(router.currentRoute.name =='Rapport'){
                   state.isLoggedIn = true
                   router.push('/report')
               }else{
               state.isLoggedIn = true
               router.push('/vente')
               }
           }
       }
    },
})