                                          item-value="id"
                                          label="Produit"
                                          persistent-hint
                                          return-object
                                          single-line
                                        ></v-autocomplete>
                                      </validation-provider>
                                    </v-col>
                                  </div>
                                  <div v-if="type=='2'">
                                    <v-col cols="12" md="12">
                                      <validation-provider>
                                        <v-autocomplete
                                          dense
                                          outlined
