<script>
import { required, digits } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("eager");

extend("digits", {
  ...digits,
  message: "{_field_} needs to be {length} digits. ({_value_})",
});

extend("required", {
  ...required,
  message: "{_field_} ne peut pas etre vide",
});


export default {
  components: {
    ValidationProvider,
    ValidationObserver,
  },
    data () {
      return {
        rapport:{fromdate:'',untildate:'',branche:{id:'',names:'',address:''},product:{id:'',names:'',category:''}},
        item: [],
        branches:[],
        products:[],
        type:null,
        menu:[{title:'Rapport General',value:''},{title:'Selon Branche',value:'0'},{title:'Selon Produit',value:'1'},{title:'Selon Branche et Produit',value:'2'}]
      }
    },
    watch: {
      menu (val) {
        val && setTimeout(() => (this.activePicker = 'YEAR'))
      },
    }, 
    computed: {
      total() {
        return this.item.reduce(
          (acc, item) => acc + item.quantity * item.amount,
          0
        )
      },
      priviledge(){
        return this.$store.state.user.priviledge
      }
    },
    filters: {
        currency(value) {
        return value.toFixed(2);
        }
    },
    methods:{
        save (date) {
        this.$refs.menu.save(date)
      },
        affichereport(){
          const form = new FormData()
          form.append('product',this.rapport.product.id)
          form.append('branche',this.rapport.branche.id)
          form.append('du', this.rapport.fromdate),
          form.append('au', this.rapport.untildate),
          form.append('role',this.$store.state.user.priviledge)
          this.$api.post('/vente_report',form)
          .then(res=>{
            this.item=res.data
          })
        },
        menurapport(id){
          this.type = id
        },
        resetform(){
          this.rapport={fromdate:'',untildate:'',branche:{id:'',names:'',address:''},product:{id:'',names:'',category:''}}
          this.item=[]
          this.type =null
        },
        getbranche(){
            this.$api.get('branches')
            .then(rs=>{
                this.branches = rs.data
            })
        },
    shproducts(){
      this.$api
      .get('/products')
      .then(res=>{
        this.products = res.data
      })
    },
      print(){
        //   this.$store.dispatch('isPrinting')
            if(this.$store.dispatch('isPrinting')){
                document.getElementById('formreport').style.display='none'
                document.getElementById('btn').style.display='none'

                document.getElementById('btnannuler').style.display='block'
            }else{
                this.$store.dispatch('isPrinting')
            }
        },
    annuler(){
        this.$store.dispatch('isPrinting')
        document.getElementById('formreport').style.display='block'
        document.getElementById('btn').style.display='block'
        document.getElementById('btnannuler').style.display='none'
    }
    },
    mounted(){
      this.getbranche()
      this.shproducts()
      document.title="Rapport | ETSPAMASA"
    },
  }
</script>
<style scoped>
    #btnannuler{
    display:none;
    }
</style>