            select:[],
            search:null,
            direct:{product:''},
            selected:{id:'',names:'',category:'',discount:'',puv:'',quantity:''},
            tabledata:[],
            resete:{names:'',id:'',discount:'',puv:'',qty:''},
            restedialog:false,
            procedeButton:false,
            loadingdirect:false,
            loadingview:{}
        }
    },
    computed:{
        currentItems(){
            return 1
        }
    },
    methods:{
        annuler(){
            this.formitems={id:'',productid:'',quantity:'',batch:'',puv:'',discount:'',names:'',category:'',qty:'',pv:''}
            this.recherche =''
        },
        createSheetItem(item){
            this.procedeButton =true
            if(item =='' || item.id =='' || item.productid=='' || item.qty ==''|| item.pv ==''){
                this.msg = 'Veuillez Completer le Formulaire.'
                this.alert = true
                this.type = 'error'
                this.procedeButton = false
                setTimeout(()=>{
                    this.alert=false
                },5000)
            }else{
                this.addeditems.push(item)
                let form  = new FormData();
                form.append('id',item.id)
                form.append('productid',item.productid)
                form.append('sheetid',this.sheetid)
                form.append('branche',this.$route.params.id)
                form.append('discount',item.discount)
                form.append('qty',item.qty)
                form.append('puv',item.pv)
                form.append('token',this.$store.state.user.token)
                this.$api.post('createSheet',form)
                .then(rs=>{
                    // this.addeditems = rs.data.records
                    if(rs.data.error){
                        this.msg = rs.data.error
                        this.alert = true
                        this.type = 'error'
                        this.procedeButton = false
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                    }
                    else{
                        this.msg = 'L\'Operation a reussit avec success.'
                        this.alert = true
                        this.type = 'success'
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                        this.sheetid = rs.data.sheet
                        this.formitems.quantity = rs.data.dqty
                        this.allItems()
                        this.allBrancheItems()
                        this.annuler()
                        this.procedeButton = false
                    }
                })
            }
        },
        itemresete(resete){
            this.resete = resete
            this.resete.names = resete.names
            this.resete.id = resete.item
            this.resete.qty = resete.qty
            this.resete.puv = resete.puv
            this.resete.discount = resete.discount
        },
        savereset(){
            let form = new FormData()
            form.append('id',this.resete.id)
            form.append('qty',this.resete.qty)
            form.append('puv',this.resete.puv)
            form.append('discount',this.resete.discount)
            this.$api.post('reset_item',form)
            .then(rs=>{
                if(!rs.data.error){
                    this.msg = "L'operation a reussit avec success."
                    this.alert = true
                    this.type = 'success'
                    this.allItems()
                    this.allBrancheItems()
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                }else{
                    this.msg = rs.data.error
                    this.alert = true
                    this.type = 'error'
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                }
            })
        },
        allItems(){
            let id = this.$route.params.id
            this.$api.get('branche_products/'+id)
            .then(rs=>{
                this.products = rs.data
            })
        },
         allBrancheItems(){
             this.datatableload =true
            let id = this.$route.params.id
            this.$api.get('archive_items/'+id)
            .then(rs=>{
                this.sheets = rs.data
                this.datatableload = false
            })
        },
        get_details(index){
            this.loadingdetails =true
          this.$api.get('product_item_info/'+index)
          .then(rs=>{
            this.datas = rs.data
            this.loadingdetails = false
          })
        },
        view(id){
            this.loadingview[id]=true
            // console.log(this.loadingview[id])
            this.$api.get('branche_items/'+id)
            .then(rs=>{
                this.items = rs.data
                this.loadingview[id]=false
            })
        },
        currentproducts(){
            this.$api.get('/items')
            .then(rs=>{
                this.select = rs.data
            })
        },
        setform(items){
        this.formitems = items
        this.dialogItem = false
        this.disabled=true
        this.formitems.qty =1
        this.formitems.quantity = items.quantity-items.used_qty
        this.formitems.pv = items.puv
        this.datas =[]

      },
      back(){
          this.$router.push('/branche')
      },
    shproducts(){
      this.$api
      .get('/products')
      .then(res=>{
        this.productdirects = res.data
      })
    },
    completesearch(selected){
        this.selected = selected
    },
    createDirectSheetItem(itemsdirect){
        if(itemsdirect.quantity ==''|| itemsdirect.puv ==''|| itemsdirect==''){
            this.msg = "Veuilez Completer le formulaire s'il vous plait."
            this.type = 'error'
            this.alert = true
            setTimeout(()=>{
                this.alert=false
            },5000)
        }
        else{
            this.tabledata.push(itemsdirect)
            this.selected=[]
            this.direct.product=[]
        }
    },
    savedirectdata(){
        this.loadingdirect = true
        if(this.tabledata == ''){
            this.loadingdirect = false
            this.msg = "La feuille est vide. Veuilez remplir la feuille s'il vous plait."
                 this.alert = true
                 this.type = 'error'
                setTimeout(()=>{
                    this.alert=false
                },5000)
        }
        else{
            let form  = new FormData()
            form.append('items',JSON.stringify(this.tabledata))
            form.append('token',this.$store.state.user.token)
            form.append('branhce',this.$route.params.id)
            this.$api.post('created_direct_data',form)
            .then(rs=>{
                if(rs.data.success){
                    this.loadingdirect = false
                    this.tabledata =[]
                    this.direct.product=[]
                    this.selected=[]
                    this.msg = "Les Nouveux produits ont ete enregistrer avec success."
                    this.alert = true
                    this.type = 'success'
                    this.allItems()
                    this.allBrancheItems()
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                }else{
                    this.loadingdirect = false
                    this.msg = rs.data.error
                    this.type = 'error'
                    this.alert = true
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                }
            })
        }
    },
    removeedirect(){
        this.tabledata =[]
        this.direct.product=[]
        this.selected=[]
    },
    annulerform(){
        this.direct.product=[]
        this.selected=[]
    },
    removedirect(indexdirect){
        this.tabledata.splice(indexdirect,1)
    }
    },
    mounted(){
        this.allItems()
        this.currentproducts()
        this.allBrancheItems()
        this.shproducts()
        document.title ="Branche Item | ETSPAMASA"
    }
}
</script>